import Vue from "vue";
import App from "./App";
import router from "./router/index";

import PaperDashboard from "./plugins/paperDashboard";
import "vue-notifyjs/themes/default.css";



const DEV_API='http://alipa.test/api/';
const PROD_API='https://alipa.zeinsoftware.com/api/';

const apiUrl=process.env.NODE_ENV === 'development'?DEV_API:PROD_API;
Vue.prototype.$apiUrl = apiUrl;

import BootstrapVue from 'bootstrap-vue'
import numerical from '@/util/directives.js'

Vue.directive('numerical',numerical);


const token=localStorage.getItem('token');

if(!token){
  if(router.path!='/acceso'){
    router.push('/acceso');
  }
  
}



Vue.use(BootstrapVue);

const Bootstrap={


  computed:{
    createFormData(){

      return function(model,ignore){

        ignore=ignore?ignore:[];

        const data = new FormData();

        for (const key in model) {

            if(ignore.indexOf(key)!==-1){
              continue;
            }

            let item=model[key];
            if (item!=undefined) {
                if(typeof item=='object' && !(item instanceof File)){
                  item=JSON.stringify(item);
                }
                data.append(key, item);
            }
        }

        return data;
      }


    },

    fillModel(){

      return function(model,data){

        for (const key in data) {
          if (model.hasOwnProperty(key) && data[key]) {

              model[key] = data[key];
          }

          
        }
      }

    },




  }



}

Vue.mixin(Bootstrap);



Vue.use(PaperDashboard);

/* eslint-disable no-new */
new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
